@import url(https://fonts.googleapis.com/css2?family=Lora&family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nerko+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-header {
  background-color: #000e1f;
  min-height: 400vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
  font-family: 'Lora', serif;
}
.blank {
  padding-top: 55px;
  height: 100px;
}

.content {
  text-align: center;
  display: flex;
  flex-direction: column;
}

div:empty {
  padding-top:25px;
  height:0;
}

.App {
  width: 100%;
  font-family: 'Lora', serif;
}

.navbar {
  background-color: #000e1f;
  height: 50px;
  border-radius: 0px;
  box-shadow: 0px 1px 10px #999;
}

.header-img {
  height: auto;
  width: 50px;
}
.header {
  padding: 0% !important;
  margin: 0%;
}

.footer {
  background-color: #000e1f;
  color: aliceblue;
  text-align: center;
}

.intro {
  background-color: #000e1f;
  text-align: center;
  justify-content: center;
  width:100%;
  margin:0;
}
.intro-image {
  height: 100%;
  max-height: 550px;
  width: 100%;
  justify-content: center;
  text-align: center;
}
.intro-text {
  color: ghostwhite;
  font-size: 6vw;
  text-align: center;
  justify-content: center;
  padding-top: 12vw;
  padding-bottom: 5vw;
  text-align: center;
}
.aboutUs {
  background-color: #e1eff2;
  text-align: center;
}
.blank {
  height: 6vw;
  background-color: white;
}
.aboutUsCard {
  background-color: #FFFFFF;
}
.people {
  background-color: #040414;
  color: aliceblue;
}
.card {
  background-color: #040414;
}

